.card1, .application-submitted {
    display: block;
    position: relative;
    max-width: 262px;
    background-color: #ffffff;
    outline: none;
    border-radius: 4px;
    border: none;
    padding: 32px 24px;
    margin: 12px;
    text-decoration: none;
    z-index: 0;
    overflow: hidden;
    box-shadow: 0px 4px 8px rgba(38, 38, 38, 0.2)
}
.card1:focus{
    box-shadow: none !important;
    color: #ffffff;
    background: #007A4D;
}

.card1:focus .small.note{
    color: #ffffff;
}

.card1:before {
     content: "";
     position: absolute;
     z-index: -1;
     top: -16px;
     right: -16px;
     background: #007A4D;
     height: 32px;
     width: 32px;
     border-radius: 32px;
     transform: scale(1);
     transform-origin: 50% 50%;
     transition: transform 0.25s ease-out;
}

.card1:hover:before {
    transform: scale(21);
}

.card1:hover p {
    transition: all 0.3s ease-out;
    color: rgba(255, 255, 255, 0.8);
}

.card1:hover h6 {
    transition: all 0.3s ease-out;
    color: #ffffff;
}

