/******************************Home Page *******************************/
.dashboardOverview {
    width: 100%;
    height: 180px;
    background: white;
    position: absolute;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.dashboardOverview h3, .referBusiness h3{
    margin-top: 50px;
    font-size: 15px;
    font-weight: bold;
}

.dashboardOverview p {
    font-weight: 300;
    margin-bottom: 20px;
    text-align: -webkit-center;
    text-align: -moz-center;
}

.referBusiness p{
    font-weight: 300;
}

.stayInTheLoop li {
    display: flex;
    align-items: center;
    margin-top: 20px;
}

.stayInTheLoop li span{
    margin-left: 15px;
}


.stayInTheLoop {
    height: 400px;
    width: 100%;
    position: absolute;
    margin-top: 400px;
    left: 0;
    display: flex;
    justify-content: center;
}

button.btn.btn-dark.application-start.home-tab-button {
    margin-top: 10px;
}

.referBusiness {
    width: 100%;
    height: 200px;
    margin-top: 180px;
    position: absolute;
    left: 0;
    display: flex;
    justify-content: center;
}

.referBusiness div, .stayInTheLoop .Content{
    width: 60%;
    height: 200px;
    background-color: white;
