    flex-direction: column;
    align-items: center;
}

.stayInTheLoop .Content{
    margin-top: 0px;
    height: 300px;
    align-items: unset;
    justify-content: unset;
}

.stayInTheLoop .Content hr{
    width: 100%;
    color: #e0e0e0;
    height: 1px;
}

.stayInTheLoop .Content p{
    margin: 10px 0px 0px 15px;
    font-weight: 300;
}

.infoListIcon {
    width: 40px;
    height: 40px;
    background: rgb(0 122 77);
    border-radius: 50%;
    color: #ffffff;
    font-size: 20px;
    justify-content: center;
    display: flex;
    align-items: center;
}

.mobile{
    display: none;
}

/*style={{height: "315px", overflowY:"scroll"}}*/
.headline-list{
    height: 470px;
    overflow-y: scroll;
}

/* learn side bar */
.left-learn-col{
    padding:0px;
    background-color: white;
}

.left-side-bar {
    width: 23%;
    height: 100%;
    background: white;
    left: 0;
    padding-top: 20px;
    position: absolute;
    overflow-y: scroll;
}


.left-side-bar::-webkit-scrollbar{
    display: none;
}

.right-col{
    display: flex;
    align-items: flex-end;
    flex-direction: column;
}


.course-price {
    width: 300px;
    height: 200px;
    background: white;
    border-radius: 20px;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.number-of-quizzes{
    width: 300px;
    min-height: 150px;
    background: white;
    border-radius: 20px;
    margin-top: 10px;
    padding-top: 20px;
    padding-bottom: 20px;
}

.number-of-quizzes::-webkit-scrollbar{
    display: none;
}

.completed-quizzes{
    width: 300px;
    height: 350px;
    background: white;
    border-radius: 20px;
    margin-top: 10px;
    padding-top: 20px;
    overflow-y: scroll;
}

.completed-quizzes::-webkit-scrollbar{
    display: none;
}

.completed-quizzes p{
    font-weight: bold;
}

.course-price p {
    margin: 5px 0px 0px 30px;
    font-weight: bold;
}

.left-side-bar p{
    font-weight: bold;
    margin-left: 30px;
}

#coursesContainer{
    margin-left: 15px;
}

.number-of-quizzes p, .completed-quizzes p{
    margin: 5px 0px 0px 30px;
}

#quizCountHeader{
    font-weight: bold;
}

#courseOverview{
    display: flex;
    margin-top: 20px;
}

#courseDescription {
    margin-left: 15px;
}

#courseDescription p{
    font-weight: 300;
    font-size: 14px;
    line-height: 27px;
}
.takeQuiz {
    width: 100%;
    height: 100%;
    background: white;
    position: fixed;
    top: 0;
    left: 0;
}

.quizContent{
    height: 100%;
    overflow-y: scroll;
}

.quizContent::-webkit-scrollbar{
    display: hidden;
}

.questionBlock {
    margin-top: 40px;
    margin-bottom: 20px;
    width: 50%;
}

.question{
    font-weight: bold;
}

.questionOption {
    font-size: 13px;
    margin-top: 10px;
    display: flex;
}

.submit-quiz {
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.submit-quiz div{
    width: 100px;
    height: 50px;
    border-radius: 10px;
}

#closeQuiz {
    position: absolute;
    left: 0;
    margin-left: 15px;
    margin-top: 10px;
    cursor: pointer;
    z-index: 99;
    display: flex;
    align-items: center;
    width: 60px;
    justify-content: space-around;
}

#takeQuizBtn{
    position: absolute;
    right: 0;
    cursor: pointer;
    z-index: 99;
    width: 200px;
    height: 50px;
    background-color: #46A16E;
    color: white;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;

}

.submit-quiz div {
    width: 200px;
    height: 39px;
    border-radius: 50px;
    background: #46a16e;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.quizErrors{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

#pastQuizScore {
    position: absolute;
    top: 0;
    right: 0;
    margin-right: 220px;
}

.previousScoreFailed{
    color: #f44336;
    margin-top: 10px;
}

.previousScorePassed{
    color: #46a16e;
    display: flex;
    margin-top: 5px;
    align-items:center;
}

.quizErrors p{
    width: 50%;
}

#buy-prompt{
    margin-top: 20px;
    text-align: -webkit-center;
    text-align: -moz-center;
    font-weight: 300;
}

.quiz {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    background: white;
    padding-left: 15px;
    padding-right: 15px;
    border-radius: 5px;
    height: 60px;
    margin-bottom: 10px;
}

.quiz p{
    margin: 0px;
    padding: 0px;
}

.quiz button, .buy-course-btn{
    border: 0px;
    background-color: #46A16E;
    border-radius: .25rem;
    color: white;
}

.buy-course-btn{
    margin-top: 20px;
}

.quiz button:focus, .buy-course-btn:focus{
    box-shadow: 0 0 0 0.25rem rgb(70 161 110 / 50%) !important;
}

#courseOverview img{
    border-radius: 5px;
}

.course-price .price {
    color: #46A16E;
    font-size: 25px;
}

#consultantContact {
    display: flex;
    flex-direction: column;
    margin-left: 30px;
    margin-top: 10px;
}

#consultantContact #consultantEmail{
    text-decoration: underline;
    color: #46A16E;
    margin-bottom: 10px;
}

.languageCard{
    box-shadow: 0px 1px 1px 1px rgb(204 205 209 / 32%);
    background-color: white;
    /* border: solid 1px gray; */
    border-radius: 15px;
    color: black;
    font-weight: 300;
    font-size: 12px;
    padding: 5px 2%;
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: space-between;
    width: 100px;
    margin-top: 5px;
    margin-left: 0px;
}

.languageStyle{
    margin: 0 auto !important;
}

#languages {
    width: 100%;
    display: grid;
    grid-template-columns: auto auto;
    display: grid;
    margin-top: 10px;
    margin-left: 30px;
}

@media (max-width:1200px) {
    .headline-list {
        height: 315px;
    }

    .desktop{
        display: none;
    }

    .mobile{
        display: block;
    }
}


@media (max-width:1000px) {

    .slick-slider.slick-vertical.slick-initialized{
        height: 365px;
    }

    .slick-slider.slick-vertical.slick-initialized  > .slick-list{
        height: 365px !important;
    }

    .headline-header{
        flex-direction: column;
    }

    .referBusiness div, .stayInTheLoop .Content{
        width: 100%;
    }

    #pdfContainer canvas{
        width: 100% !important;
    }
}

@media (max-width: 800px){
    .home-testimonials .service-cards{
        height: unset !important;
    }
}

@media (max-width: 767px){
    .ads{
        margin-bottom: 75px;
    }
}

@media (max-width: 425px){
    .slick-slider.slick-vertical.slick-initialized{
        height: 190px;
    }

    .slick-slider.slick-vertical.slick-initialized  > .slick-list{
        height: 190px !important;
    }
}

@media (max-width:500px){
    .course-price, .number-of-quizzes, .completed-quizzes{
        width: 100% !important;
        min-height: 150px !important;
    }

    .completed-quizzes{
        height: 300px !important;
        margin-bottom: 30px;
    }

    #pastQuizScore{
        margin-top: 46px;
        background: white;
        padding: 5px;
        border-bottom: 0.5px solid;
        margin-right: 0px !important;
    }
}

